.navigation {
	margin-top: 50px;
	width: 100%;
	height: 15%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: center;
	    justify-content: center;
	padding-bottom: 70px;
}
.navigation ul {
	-webkit-padding-start: 0px;
	display: -ms-flexbox;
	display: flex;
	width: 20%;
}
.navIcon {
	-ms-flex: 1 1;
	    flex: 1 1;
	padding: 5%;
}
a img {
	height: 90%;
	width: 90%;
}
@media all and (max-width: 900px) {
	.navigation {
		margin-top: 2%;
		padding-bottom: 10%;
		height: 60%;
	}
	.navigation ul {
		width: 40%;
	}
}



